.form{
  position: absolute;
  width: 600px;
  height: 320px;
  border-radius: 20px;
  top: 30%;
  left:35%;
  text-align: center;
  border: 2px solid black;
}

.input-wrapper{
  margin-top:15px;
  margin-left: auto;
  margin-right: auto;
  box-shadow: #000000 -5px -5px 11px;
  border-radius: 20px;
  width: 325px;
  height: 55px;
  display: flex;
  align-items: center;
}
.input-wrapper input{
  background: transparent;
  border:none;
  width: 100%;
  height: 100%;
  border-radius: 20px;
  text-indent: 20px;
  color:black;
  font-size: 14px;
  outline:0;
}
